@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&family=Roboto:ital,wght@0,300;0,400;0,700;1,100&display=swap);
#root{
    height: 100vh;
    overflow: hidden;
}

.highlight{
    background-color: #99db9d;
    display:inline-block;

}
.non-highlight{
    display: inline-block;
}
.highlight_red{
    background-color: #e67c87;
    display:inline-block;

}

.hide{
    display: none !important;
}

.show{
    display: inline-block !important;
    position: relative !important;
    top: 0px !important;
    right: 0px !important;
}
.highlight:hover{
    background-color: #999fdb;
}
  
  ::selection {
    color: rgb(0, 0, 0);
    background: yellow;
  }
